// Import all components
import { ActiveState } from './components/active-state';
import { AutoResize } from './components/auto-resize';
import { AutoSuggest } from './components/auto-suggest';
import { Card } from './components/card';
import { Carousel } from './components/carousel';
import { CharacterCounter } from './components/character-counter';
import { Charts } from './components/charts';
import { Checkable } from './components/checkable';
import { Clearable } from './components/clearable';
import { Closable } from './components/closable';
import { Collapsible } from './components/collapsible';
import { Conditional } from './components/conditional';
import { CustomGreeting } from './components/custom-greeting';
import { DatePicker } from './components/date-picker';
import {FlatDatePicker} from './components/flat-datepicker';
import { Filter } from './components/filter';
import { Maps } from './components/maps';
import { Modal } from './components/modal';
import { Navigation } from './components/navigation';
import { Pagination } from './components/pagination';
import { PasswordValidator } from './components/password-validator';
import { PasswordVisibility } from './components/password-visibility';
import { Popover } from './components/popover';
import { Selector } from './components/selector';
import { SharePrice } from './components/share-price';
import { Slider } from './components/slider';
import { Status } from './components/status';
import { Toast } from './components/toast';
import { Toggable } from './components/toggable';
import { ToggableGroup } from './components/toggable-group';
import { Tooltip } from './components/tooltip';
import { Untouched } from './components/untouched';
import { Upload } from './components/upload';
import { Video } from './components/video';

import 'babel-polyfill';

// List all components.
const components = [
	ActiveState,
	AutoResize,
	AutoSuggest,
	Card,
	Carousel,
	CharacterCounter,
	Charts,
	Checkable,
	Clearable,
	Closable,
	Collapsible,
	Conditional,
	CustomGreeting,
	DatePicker,
	FlatDatePicker,
	Filter,
	Maps,
	Modal,
	Navigation,
	Pagination,
	PasswordValidator,
	PasswordVisibility,
	Popover,
	Selector,
	SharePrice,
	Slider,
	Status,
	Toast,
	Toggable,
	ToggableGroup,
	Tooltip,
	Untouched,
	Upload,
	Video
];

// initialize all components
for (let i = 0; i < components.length; i = i + 1) {
	const componentsList = document.querySelectorAll(components[i].getSelector());
	if (componentsList.length) {
		for (const component of componentsList) {
			new components[i](component);
		}
	}
}

